import React, { useState, useEffect } from 'react';
import TabNFT from './TabNFT';
import { useRoyaltyDistribution } from "./hooks/customHooks";
import { useUser } from "./hooks/userContext";


const UserInfo = () => {

    //user,rd 
    const { user } = useUser();
    const { data, isLoading } = useRoyaltyDistribution();

    //return
    if ((user === null || user === undefined)) return <div> </div>;
    if (!data) return <div></div>;

    if (isLoading) return <div>Loading...</div>;
    const rd = data.filter(rd => rd.recipient === user.userHash);

    //
    let ownedRoyalty = 0;
    if (rd.length !== 0) {
        for (let i=0; i<rd.length; i++) {
            ownedRoyalty += Number(rd[i].amount);
        }
    } 

    return (
        <div>
            <TabNFT />
            <h1></h1>
            <h2>{user.name}</h2>
            <h3>: {ownedRoyalty} ETH</h3>
            <h4>: {user.occupation} <br></br>: {user.location}</h4>
            <p>UserID: {user.userId}</p>
            <p>UserHash: {user.userHash}</p>
            <img src={"/woodnft-demo/user/" + user.imageUrl} alt={user.name} />
        </div>
    )
}

export default UserInfo;