import React from 'react';
import { Line } from 'react-chartjs-2';
import 'chartjs-adapter-luxon';
import 'chart.js/auto';
import { useRoyaltyDistribution } from './hooks/customHooks';
import { useUser } from './hooks/userContext';

const RoyalityGraph = () => {

  //user
  const { user } = useUser();
  console.log('user:', user);

  //csv
  const { data, isLoading } = useRoyaltyDistribution();
  console.log('transactions:', data);

  //return
  if((user === null || user === undefined)) return <div> </div>;
  if (!data) return <div></div>;


  if (isLoading) return <div>Loading...</div>;
  const rd = data.filter(rd => rd.recipient === user.userHash);
  if (rd.length === 0) return <div>royalty distribution not found, userId: {user.userId}</div>;
  console.log('rd', rd);


  // ISO 8601
  function convertDateToISO(dateString) {
    const [datePart, timePart] = dateString.split(' ');
    const [year, month, day] = datePart.split('/').map(part => part.padStart(2, '0'));
    const [hour, minute, second] = timePart.split(':').map(part => part.padStart(2, '0'));
    return `${year}-${month}-${day}T${hour}:${minute}:${second}`;
  }

  //
  let rdPerTokenId = []
  for (let i=0; i<rd.length; i++) {
    const currentRd = rd[i];
    let objIndex = rdPerTokenId.findIndex(obj => obj.tokenId === currentRd.tokenId);

    //rdPerTokenIdtokenIDamount
    if (objIndex === -1) {
      let obj = {
        recipient: currentRd.recipient,
        tokenId: currentRd.tokenId,
        date: convertDateToISO(currentRd.date),
        amount: Number(currentRd.amount)
      }
      rdPerTokenId.push(obj);
    } else {
      rdPerTokenId[objIndex].amount += currentRd.amount;
    }
  }

  console.log('rdPerTokenId before processing:', rdPerTokenId);

  //
  rdPerTokenId.sort((a, b) => new Date(a.date) - new Date(b.date));
  let rdAccumulation = rdPerTokenId.slice();
  let currentAmount = 0;
  for (let i=0; i<rdPerTokenId.length; i++) {
    const currentRd = rdAccumulation[i];
    currentAmount += parseFloat(currentRd.amount);
    currentRd.amount = currentAmount;
  }

  // 
  const chartData = {
    labels: rdAccumulation.map(item => item.date), // X
    datasets: [
      {
        label: 'NFT Amount',
        //data: rdAccumulation.map(item => item.amount), // Y
        data: rdAccumulation.map(item => ({x: item.date, y: item.amount, tokenId: item.tokenId})), // X, YtokenId
        borderColor: 'rgb(75, 192, 192)',
        backgroundColor: 'rgba(75, 192, 192, 0.2)',
      }
    ]
  };

  console.log('rdAccumulation:', rdAccumulation);
  console.log('chartData:', chartData);

  //
  const chartOptions = {
    scales: {
      x: {
        type: 'time',
        time: {
          //parser: 'YYYY-MM-DDTHH:mm:ss', // LuxonMoment.js
          //tooltipFormat: 'lll',
          unit: 'day'
        },
        title: {
          display: true,
          text: 'Date'
        },
        // 1
        min: '2024-02-02',
        max: '2024-02-09'
      }
    },
    plugins: {
      tooltip: {
        callbacks: {
          // 
          beforeTitle: function(context) {
            // dataIndex
            const dataIndex = context[0].dataIndex;
            const datasetIndex = context[0].datasetIndex;
            // chartDatatokenId
            const tokenId = chartData.datasets[datasetIndex].data[dataIndex].tokenId;
            return `Token ID: ${tokenId}`;
          },
          label: function(context) {
            // : "Amount: 0.06"
            const label = context.dataset.label || '';
            const value = context.parsed.y; // Chart.js 3.xcontext.parsed.y
            return `${label}: ${value}`;
          }
        }
      }
    },
    // ...
  };




  const graphStyle = {
      textAlign: 'center',
      //display: 'flex',
      backgroundColor: '#fafafa',
      width: '600px',
      height: '500px',
      //transform: `scale(1)`, 
      //transformOrigin: 'center',
      boxSizing: 'border-box',
      //border: '1px solid #FF0000',
      //padding: '50px',
      //margin: '-20% -10%',
      borderRadius: '50px', // 
      border: '1px solid #666666', // 

  };


  return (
      <div style={graphStyle}>
          <Line data={chartData} options={chartOptions} />
      </div>
      
  );

}

export default RoyalityGraph;